.objectModal {
    border: 5px solid black;
    background-color: #F4F1ED;
    outline: none !important;
    width: 70vw;
    height: 73vh;
    overflow-y: initial !important;

    position: fixed;
    display: flex;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .react-pdf__Page {
        height: 72.5vh;
        overflow-y: auto;
    }

    #modal-body {
        height: 72.5vh;
        overflow-y: auto;

        .modal-document {
            width: 70vw;
        }
    }
}